import { createSlice, PayloadAction } from '@reduxjs/toolkit'

export interface ITodo {
    id:string,
    task:string,
    checked:boolean,
    start:boolean,
    done:boolean
}
export interface TodosState {
    todos: ITodo[]
}

const initialState: TodosState = {
    todos: [{ id: '01', task: '!', checked: false, start: false, done: false }]
}

export const todosSlice = createSlice({
    name: 'todos',
    initialState,
    reducers: {
        addTodo: (state, action: PayloadAction<ITodo>) => {
            state.todos.push(action.payload);
        },
        updateTodo: (state, action: PayloadAction<ITodo>) => {
            state.todos = state.todos.map(todoObj => 
                action.payload.id === todoObj.id ? action.payload : todoObj);
        },
        deleteTodo: (state, action: PayloadAction<string>) => {
            state.todos = state.todos.filter(todoObj => todoObj.id !== action.payload);
        },
    },
})

// Action creators are generated for each case reducer function
export const { addTodo, updateTodo, deleteTodo } = todosSlice.actions
export default todosSlice.reducer